.navMenu {
  display: flex;
  align-items: center;
}

.navItem {
  font-size: 1.25rm;
  color: gray;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0 2rem;
  height: 100%;
  cursor: pointer;
}

.navBar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding-left: 3rem;
  background: white;
  height: 70px;
  display: flex;
  justify-content: space-between;
  border-bottom: sold 1px;
  --tw-bg-opacity: 1;
  border-color: rgba(229, 231, 235, var(--tw-bg-opacity));
}
